import G6 from "@antv/g6/build/g6";
import { uniqueId } from '../../utils'
// G6自带的一些图形
import Shape from '@antv/g/src/shapes'
const customNode = {
  init() {
    // G6注册自定义节点 customNode
    G6.registerNode("customNode", {
      /**
       * 绘制节点，包含文本
       * @param  {Object} cfg 节点的配置项
       * @param  {G.Group} group 节点的容器
       * @return {G.Shape} 绘制的图形，通过 node.get('keyShape') 可以获取到
       */
      draw(cfg, group) {
        // 获取配置中的size
        let size = cfg.size;
        // 如果没有定义size，则初始化为170*34
        if(!size){
          size=[170,34]
        }
        // 此处必须是NUMBER 不然bbox不正常
        const width = parseInt(size[0]);
        const height = parseInt(size[1]);
        // 获取配置中的color属性（方框左边小框的颜色）
        const color = cfg.color;
        // 此处必须有偏移 不然drag-node错位
        const offsetX = -width / 2
        const offsetY = -height / 2
        const mainId = 'rect' + uniqueId()
        // 节点中的主框
        const shape = group.addShape("rect", {
          attrs: {
            // rectID
            id: mainId,
            // 计算过偏移后的X和Y坐标
            x: offsetX,
            y: offsetY,
            // 主框的宽与高
            width: width,
            height: height,
            // 设置用于笔触的颜色、渐变或模式
            stroke: "#ced4d9",
            // 设置用于填充绘画的颜色、渐变或模式
            fill: '#fff',//此处必须有fill 不然不能触发事件
            // 圆角属性
            radius: 4,
          }
        });
        // 节点中的左边的小方框
        group.addShape("rect", {
          attrs: {
            x: offsetX,
            y: offsetY,
            // 宽度4
            width: 4,
            height: height,
            // 填充小圆框的颜色
            fill: color,
            // 父节点
            parent: mainId,
            // 圆角属性 分别左上、右上、右下、左下
            radius: [4, 0, 0, 4]
          }
        });
        // 用于动画的长方形框
        group.addShape("rect", {
          zIndex: -99,
          attrs: {
            x: offsetX,
            y: offsetY,
            width: width,
            height: height,
            // 设置用于填充绘画的颜色、渐变或模式
            fill: '#ced4d9',
            // 圆角属性
            radius: 8,
            opacity: 0.3,
            isAnimate: true
          }
        });
        // 节点中左边的图标（包括缩略图） 属性：image
        group.addShape("image", {
          attrs: {
            x: offsetX + 16,
            y: offsetY + 8,
            width: 16,
            height: 16,
            img: cfg.image,
            parent: mainId
          }
        });
        // 节点中右边的状态图标 属性：stateImage
        group.addShape("image", {
          attrs: {
            x: offsetX + width - 32,
            y: offsetY + 8,
            width: 16,
            height: 16,
            parent: mainId,
            img: cfg.stateImage
          }
        });
        // 如果新建节点配置中有backImage属性，定义为背景图片节点
        if(cfg.backImage){
          // 新建一个夹子图形
          const clip = new Shape.Rect({
            attrs: {
              x: offsetX,
              y: offsetY,
              width: width,
              height: height,
              fill:'#fff',
              radius: 4
            }
        });
          // 以上面的夹子图形clip建立图片节点
          group.addShape("image", {
            attrs: {
              x: offsetX,
              y: offsetY,
              width: width,
              height: height,
              img: cfg.backImage,
              clip: clip
            }
          });
        }
        // 如果节点的配置中有label属性，为节点添加label
        if (cfg.label) {
           group.addShape("text", {
            attrs: {
              id: 'label' + uniqueId(),
              x: offsetX + width / 2,
              y: offsetY + height / 2,
              textAlign: "center",
              textBaseline: "middle",
              text: cfg.label,
              parent: mainId,
              fill: "#565758"
            }
          });
        }
        // 如果节点的配置中有inPoints属性，为节点添加【anchor 输入点】
        if (cfg.inPoints) {
          for (let i = 0; i < cfg.inPoints.length; i++) {
            let x,
              y = 0;
            //0为顶 1为底
            if (cfg.inPoints[i][0] === 0) {
              y = 0;
            } else {
              y = height;
            }
            x = width * cfg.inPoints[i][1];
            const id = 'circle' + uniqueId();
            // 为节点添加anchor输入点的可视化图形
            // 选中节点anchor时的淡蓝色圆框
            group.addShape("circle", {
              attrs: {
                id: 'circle' + uniqueId(),
                parent: id,
                x: x + offsetX,
                y: y + offsetY,
                // 半径为10
                r: 10,
                // boolean值，用于判断【当前是否为等待接入的提示状态】
                isInPointOut: true,
                // 圆形图形内部颜色
                fill: "#1890ff",
                opacity: 0
              }
            });
            // 选中节点anchor时anchor的圆形
            group.addShape("circle", {
              attrs: {
                id: id,
                x: x + offsetX,
                y: y + offsetY,
                // 半径为3
                r: 3,
                // boolean值，用于判断【当前被选中的状态】
                isInPoint: true,
                // 圆形图形内部颜色
                fill: "#fff",
                // 圆形图形边框颜色
                stroke: "#1890ff",
                // 外边框透明值
                opacity: 0
              }
            });
          }
        }
        // 如果节点的配置中有outPoints属性，为节点添加【anchor 输出点】
        if (cfg.outPoints) {
          for (let i = 0; i < cfg.outPoints.length; i++) {
            let x,
              y = 0;
            //0为顶 1为底
            if (cfg.outPoints[i][0] === 0) {
              y = 0;
            } else {
              y = height;
            }
            x = width * cfg.outPoints[i][1];
            const id = 'circle' + uniqueId()
            // 为节点添加anchor输出点的可视化图形
            group.addShape("circle", {
              attrs: {
                id: 'circle' + uniqueId(),
                parent: id,
                x: x + offsetX,
                y: y + offsetY,
                r: 10,
                // // boolean值，用于判断【当前是否为等待接入的提示状态】
                isOutPointOut: true,
                fill: "#1890ff",
                opacity: 0//默認0 需要時改成0.3
              }
            });
            group.addShape("circle", {
              attrs: {
                id: id,
                x: x + offsetX,
                y: y + offsetY,
                r: 3,
                // // boolean值，用于判断【当前被选中的状态】
                isOutPoint: true,
                fill: "#fff",
                stroke: "#1890ff",
                opacity: 0
              }
            });
          }
        }
        group.sort()
        // 添加文本、更多图形
        return shape;
      },
      /**
       * 响应节点的状态变化，主要是交互状态，业务状态请在 draw 方法中实现
       * 默认情况下，节点的 keyShape 将会响应 selected、active 状态，有其他状态需求的用户自己复写这个方法
       * @param  {String} name 状态名称
       * @param  {Object} value 状态值
       * @param  {Node} item 节点
       */
      setState(name, value, item) {
        // 获取节点的容器
        const group = item.getContainer();
        // 因为使用了group.sort() 所以 children[0] 是动画rect children[1] 才是 keyShape
        const shape = group.get("children")[1]; // 顺序根据 draw 时确定

        const children = group.findAll(g => {
          return g._attrs.parent === shape._attrs.id
        });
        const circles = group.findAll(circle => {
          return circle._attrs.isInPoint || circle._attrs.isOutPoint;
        });
        // 定义被选中时的style
        const selectStyles = () => {
          shape.attr("fill", "#f3f9ff");
          shape.attr("stroke", "#6ab7ff");
          shape.attr("cursor", "move");
          children.forEach(child => {
            child.attr("cursor", "move");
          });
          circles.forEach(circle => {
            circle.attr('opacity', 1)
          })
        };
        // 定义未被选中时的style
        const unSelectStyles = () => {
          shape.attr("fill", "#fff");
          shape.attr("stroke", "#ced4d9");
          circles.forEach(circle => {
            circle.attr('opacity', 0)
          })
        };
        const addEdgeHoverStyles = () => {
            shape.attr("fill", "#f3f9ff");
            shape.attr("stroke", "#6ab7ff");
            circles.forEach(circle => {
                circle.attr('opacity', 1)
            })
        };
        // 定义节点出现错误的style
        const addErrorStyles = () => {
          shape.attr("fill", "#fff");
          shape.attr("stroke", "#FF0000");
          circles.forEach(circle => {
            circle.attr("opacity", 1);
          });
        };
        switch (name) {
          // 如果被选中或者鼠标放在节点上时，如果有value值则设为选中状态，否则为未选中状态
          case "selected":
          case "hover":
            if (value) {
              selectStyles()
            } else {
              unSelectStyles()
            }
            break;
          case "addEdgeHover":
            if(value) {
                addEdgeHoverStyles();
            } else {
                unSelectStyles();
            }
            break;
          case "running":
            if(value) {
              // 遍历 group 找到存在 isAnimate 的 shape
              group.find( item => {
                if(item._attrs.isAnimate) {
                  // 为其添加动画效果
                  item.animate({
                    x: item._attrs.x - 10,
                    y: item._attrs.y - 10,
                    width : item._attrs.width + 20,
                    height: item._attrs.height + 20,
                    repeat: true
                  }, 1000, 'easeLinear');
                }
              })
            } else {
              group.find( item => {
                if(item._attrs.isAnimate) {
                  item.stopAnimate();
                  item.attr('width', item._attrs.width - 20);
                  item.attr('height', item._attrs.height - 20);
                  item.attr('x', item._attrs.x + 10);
                  item.attr('y', item._attrs.y + 10);
                }
              })

            }
            break;
          case "error":
            if (value) {
              addErrorStyles();
            } else {
              unSelectStyles();
            }
            break;
        }
      }
    });
  }
}

export default customNode
