export default (_self, h) => {
    // 返回了一个Array
    return [
        h(
            // 标签名称
            "el-upload",
            // 一个与模板中属性对应的数据对象。
            {
                // 组件prop
                props: {
                    value: _self.obj.value || ""
                },
                // 普通 HTML 特性
                attrs: {
                    listType: "picture-card",
                    action: "#",
                    autoUpload: false
                },
                // 事件监听器 on 属性中
                on: {

                }
            },
            // 子级虚拟节点 (VNodes)，由 `createElement()` 构建而成
            [h("i",{
                class: {
                    'el-icon-plus': true
                }
            })]
        )
    ];
};


export let fileConf = {
    // 对应数据库内类型
    type: 'file',
    // 是否可配置
    config: true,
    value: '',
    attr: {
        label: '上传文件',
        require: true,
    },
    attrName: [
        {
            name: '标题',
            attribute: 'label',
            type: 'input',
            clientType: 'sell'
        },
        {
            name: '是否必填',
            attribute: 'require',
            type: 'singleCheck',
            clientType: 'sell'
        },
    ]

}
