import eventBus from "../utils/eventBus";
export default {
    getEvents() {
        return {
            'canvas:click':'onCanvasClick'
        };
    },
    /**
     * 响应 canvas 点击事件，使detailPanel重置为画布
     */
    onCanvasClick(){
        // 通过eventBus，触发canvasClick事件
        eventBus.$emit('detailPanelReset');
    }
};
