<template>
  <div class="detailpannel">
    <div>
      <div v-if="status === 'node-selected'" class="pannel" id="node_detailpannel">
        <div class="pannel-title">节点详情</div>
        <div class="block-container">
          <el-row :gutter="10">
            <!--以下为节点的常规属性-->
            <div class="attr-container">
              <el-col :span="9">节点ID：</el-col>
              <el-col :span="15">
                {{node.id}}
              </el-col>
            </div>
            <div class="attr-container">
              <el-col :span="9">
                <p>名称</p>
              </el-col>
              <el-col :span="15">
                <el-input
                        v-model="node.name"
                        @change="handleChangeName"
                        :disabled="node.mType === 'startNode' || node.mType === 'endNode' || node.mType === 'cycleNode' || node.nodeKind === 6 || node.nodeKind === 2"/>
              </el-col>
            </div>
            <!--<el-col :span="8">颜色</el-col>-->
            <!--<el-col :span="16">-->
              <!--<el-color-picker-->
                <!--v-model="node.color"-->
                <!--:disabled="node.mType === 'startNode' || node.mType === 'endNode' || node.mType === 'cycleNode'"-->
                <!--@change="handleChangeColor">-->
              <!--</el-color-picker>-->
            <!--</el-col>-->
            <!--以下为节点的自定义属性-->
            <div v-for="attrName in node.attrName" :key="attrName.name" class="attr-container">
              <div v-if="attrName.clientType === 'sell'">
                <el-col :span="9">
                  <p style="color: #f56c6c; margin-right: 4px;" v-if="attrName.required">*</p>
                  <p>{{attrName.name}}</p>
                </el-col>
                <el-col :span="15">
                  <!-- 文本输入 -->
                  <el-input
                          v-model="node.attr[attrName.attribute]"
                          @change="handleChangeInput(...arguments, attrName.attribute)"
                          v-if="attrName.type === 'input' && attrName.clientType === 'sell'">
                  </el-input>
                  <!-- 数字输入 -->
                  <el-input-number
                          v-model="node.attr[attrName.attribute]"
                          @change="handleChangeNumber(...arguments, attrName.attribute)"
                          v-if="attrName.type === 'inputNumber' && attrName.clientType === 'sell'"
                          :min="attrName.min"
                          :max="attrName.max"
                          :precision="0">
                  </el-input-number>
                  <!-- 单选选择 -->
                  <el-select
                          v-model="node.attr[attrName.attribute]"
                          placeholder="请选择"
                          v-if="attrName.type === 'select' && attrName.clientType === 'sell'"
                          filterable
                          clearable
                          @change="handleChangeSelect(...arguments, attrName.attribute)">
                    <el-option
                            v-for="item in attrName.options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                  </el-select>
                  <!-- 多选选择 -->
                  <el-select
                          v-model="node.attr[attrName.attribute]"
                          placeholder="请选择"
                          v-if="attrName.type === 'multiSelect' && attrName.clientType === 'sell'"
                          multiple
                          filterable>
                    <el-option
                            v-for="item in attrName.options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                    </el-option>
                  </el-select>
                  <!-- 切换两种状态CheckBox -->
                  <el-checkbox
                          v-model="node.attr[attrName.attribute]"
                          v-if="attrName.type === 'singleCheck' && attrName.clientType === 'sell'"
                          style="line-height: 40px;">
                  </el-checkbox>
                </el-col>
              </div>
            </div>
          </el-row>
        </div>
      </div>
      <div v-if="status==='canvas-selected'" class="pannel" id="canvas_detailpannel">
        <div class="pannel-title">画布</div>
        <div class="block-container">
          <el-checkbox v-model="showGrid" @change="changeGridState">网格对齐</el-checkbox>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import eventBus from "../../utils/eventBus";
import Grid from "@antv/g6/build/grid";
import { formAPI, userAPI, editorAPI } from "../../../../globalAPI";
import { deepClone } from "../../../../utils";
export default {
  data() {
    return {
      status: "canvas-selected",
      showGrid: false,
      page: {},
      graph: {},
      item: {},
      node: {},
      grid: null,
      // 本地表单List
      formList: [],
      // 本地用户List 原始数据
      userList: [],
      // 通过 groupID 筛选过后的用户List 供 select 使用
      filterUserList: [],
      // 本地用户组List
      userGroupList: [],
      // 本地流程List
      processList: []
    };
  },
  created() {
    this.getUserList();
    this.getFormList();
    this.getUserGroupList();
    this.getProcessList();
    this.init();
    this.bindEvent();
  },
  methods: {
    init() {},
    /**
     * 绑定点击行为
     */
    bindEvent() {
      let self = this;
      eventBus.$on("afterAddPage", page => {
        self.page = page;
        self.graph = self.page.graph;
        eventBus.$on("nodeselectchange", item => {
          if (item.select === true && item.target.getType() === "node") {
            // 更改节点状态
            self.status = "node-selected";
            // 深拷贝当前节点 model
            let data = deepClone(item.target.getModel());
            // 给节点绑定上最新的数据
            for(let i in data.attrName) {
              switch (data.attrName[i].attribute) {
                case "bindForm":
                  data.attrName[i].options = this.formList;
                  break;
                case "inChargeGroupID":
                  data.attrName[i].options = this.userGroupList;
                  break;
                case "roomGroups":
                  data.attrName[i].options = this.userGroupList;
                  break;
                case "processID":
                  data.attrName[i].options = this.filterCanUse(this.processList);
                  break;
              }
            }
            // 调用 graph 方法更新数据
            this.graph.updateItem(item.target, data);
            let updateItem = this.graph.findById(data.id);
            self.item = updateItem;
            self.node = updateItem.getModel();
          } else {
            self.status = "canvas-selected";
            self.item = null;
            self.node = null;
          }
        });
        // 监视点击canvas事件，触发时重置panel为画布模式
        eventBus.$on("detailPanelReset", function() {
            self.status = "canvas-selected";
            self.item = null;
            self.node = null;
        });
      });
    },
    /**
     * 当节点的名字发生改变时，对graph中的数据进行更新
     * @param e 改变的值
     */
    handleChangeName(e) {
      let realLength = e.replace(/[\u0391-\uFFE5]/g,"aa").length;
      const model = {
        name: e,
        label: e
      };
      if(realLength >= 14) {
        model.label = e.substr(0, 7) + "...";
      }

      this.graph.update(this.item, model);
    },
    /**
     * 当节点的颜色发生改变时，对graph中的数据进行更新
     * @param e 改变的值
     */
    handleChangeColor(e) {
      const model = {
        color: e
      };

      this.graph.update(this.item, model);
    },
    /**
     * 当类型为input的自定义属性值改变时，对graph中的数据进行更新
     * @param value 输入的值
     * @param attrName 改变的属性值
     */
    handleChangeInput(value, attrName) {
      const model = {
          attr: {}
      };
      model.attr[attrName] = value;
      this.graph.update(this.item, model);
    },
    /**
     * 当类型为number的自定义属性值改变时，对graph中的数据进行更新
     * @param value 输入的值
     * @param oldValue 上一个值 unused
     * @param attrName 改变的属性值
     */
    handleChangeNumber(value, oldValue, attrName) {
      const model = {
          attr: {}
      };
      model.attr[attrName] = value;
      this.graph.update(this.item, model);
    },
    /**
     * 当select进行选择时触发的事件
     * @param value 改变的值
     * @param attrName
     */
    handleChangeSelect(value, attrName) {
      // 判断触发该事件的 select 是否为 inChargeGroupID 的 select
      if(attrName === "inChargeGroupID") {
        // 遍历数组 将与所选值不一样的清除
        for(let i in this.node.attr["roomGroups"]) {
          if(this.node.attr["roomGroups"][i] !== value) {
            this.node.attr["roomGroups"].splice(i, 1);
          }
        }
        // 如果其中不存在该值 则往数组中添加该值
        if(!this.node.attr["roomGroups"].includes(value)) {
          this.node.attr["roomGroups"].push(value);
        }
        // 通过 groupID 更新对应的 userList
        this.handleFilterUserList(value);
      }
    },
    changeGridState(value) {
      if (value) {
        this.grid = new Grid();
        this.graph.addPlugin(this.grid);
      } else {
        this.graph.removePlugin(this.grid);
      }
    },
    /**
     * ajax 请求获取 formList
     */
    getFormList() {
      this.axios
              .post(formAPI.getFormListByUser)
              .then(response => {
                if (response.data.resultCode === 1) {
                  for(let i in response.data.objData) {
                    let item = {};
                    item.value = response.data.objData[i].id;
                    item.label = response.data.objData[i].name;
                    this.formList.push(item);
                  }
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: error.response.data.message
                });
              });
    },
    /**
     * ajax 请求获取 UserList
     */
    getUserList() {
      this.axios
              .post(userAPI.getSonUsers)
              .then(response => {
                if (response.data.resultCode === 1) {
                  this.userList = response.data.objData;
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: error.response.data.message
                });
              });
    },
    /**
     * ajax 请求获取 userGroupList
     */
    getUserGroupList() {
      this.axios
              .post(userAPI.getUserGroups)
              .then(response => {
                if (response.data.resultCode === 1) {
                  for(let i in response.data.objData) {
                    let item = {};
                    item.value = response.data.objData[i].groupID;
                    item.label = response.data.objData[i].groupName;
                    this.userGroupList.push(item);
                  }
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: error.response.data.message
                });
              });
    },
    /**
     * ajax 请求获取 processList
     */
    getProcessList() {
      this.axios
              .post(editorAPI.getProcessListByUser)
              .then(response => {
                if (response.data.resultCode === 1) {
                  for(let i in response.data.objData) {
                    let item = {};
                    item.value = response.data.objData[i].id;
                    item.label = response.data.objData[i].name;
                    item.canUse = response.data.objData[i].canUse;
                    this.processList.push(item);
                  }
                }
              })
              .catch(error => {
                this.$message({
                  type: "error",
                  message: error.response.data.message
                });
              });
    },
    filterCanUse(arr) {
      let temp = [];
      for (let item of arr) {
        console.log(item);
        if(item.canUse === 1) {
          temp.push(item);
        }
      }
      return temp;
    },
    handleFilterUserList(groupID) {
      // 置空
      this.filterUserList = [];
      // 遍历原始数组 寻找符合条件的元素
      for(let user of this.userList) {
        if(user.userGroupID.includes(groupID)) {
          let item = {};
          item.value = user.userID;
          item.label = user.userName;
          this.filterUserList.push(item);
        }
      }
      // 清空 select 绑定的值
      this.node.attr.inChargeUserID = "";
      // 赋值给 attrName 中的 option
      let data = deepClone(this.node);
      for(let i in data.attrName) {
        if(data.attrName[i].attribute === "inChargeUserID") {
          data.attrName[i].options = this.filterUserList;
        }
      }
      // 查找需要更新的 item
      let updateItem = this.graph.findById(data.id);
      // 更新操作
      this.graph.updateItem(updateItem, data);
      // 查找更新后的 item
      let afterUpdateItem = this.graph.findById(data.id);
      // 对应赋值
      this.item = afterUpdateItem;
      this.node = afterUpdateItem.getModel();
    }
  }
};
</script>

<style scoped>
.detailpannel {
  height: 100%;
  position: absolute;
  right: 0px;
  z-index: 2;
  background: #f7f9fb;
  width: 300px;
  border-left: 1px solid #e6e9ed;
  font-size: 12px;
}
.detailpannel .block-container {
  padding: 16px 8px;
}
.block-container .el-col {
  display: flex;
  align-items: center;
  margin-bottom: 8px;
}
.pannel-title {
  height: 32px;
  border-top: 1px solid #dce3e8;
  border-bottom: 1px solid #dce3e8;
  background: #ebeef2;
  color: #000;
  line-height: 28px;
  padding-left: 12px;
}
.attr-container {
  overflow-x: hidden;
}
</style>
