const mutations = {
    /**
     * 登录后设置user对象
     */
    setUser: (state, payload) =>{
        state.isLogin = true;
        state.user = payload;
    },
    /**
     * 注销，登出
     */
    signOut: (state) => {
        state.user = {};
        sessionStorage.clear();
        state.isLogin = false;
    }
}

export default mutations
