import record from "../../../../../static/record.png"
export default (_self, h) => {
    // 返回了一个Array
    return [
        h(
            "el-image",
            {
                props: {
                    src: record
                }
            }
        )
    ]
};


export let recordConf = {
    // 对应数据库内类型
    type: 'record',
    // 是否可配置
    config: true,
    value: '',
    attr: {
        label: '录音',
        require: true,
    },
    attrName: [
        {
            name: '标题',
            attribute: 'label',
            type: 'input',
            clientType: 'sell'
        },
        {
            name: '是否必填',
            attribute: 'require',
            type: 'singleCheck',
            clientType: 'sell'
        },
    ]

}
