import { uniqueId } from '../../utils';
import eventBus from "../../utils/eventBus";

/**
 * @param id Editor的ID
 * @param graph Editor对应的 g6 graph 对象
 */
export default class Editor {
  /**
   * 构造函数
   */
  constructor() {
    // 随机一个 id
    this.id = uniqueId();
  }
  getGrpah() {
    return this.graph
  }

  /**
   *
   * @param event 事件名
   * @param params 传参的具体参数
   */
  emit(event, params) {
    if (event === 'afterAddPage') {
      // 若 event 为 afterAddPage 则初始化 Editor 类中的 graph 属性
      this.graph = params.graph
    }
    // 利用 eventBus 触发 ItemPanel 中 afterAddPage 事件，传参 params
    eventBus.$emit(event, params)
  }
  on(event) {
    switch (event) {
      case 'changeNodeData':
        this.graph.refresh()
        break
    }
  }

  /**
   * graph 增加一个元素
   * @param type 元素的 type
   * @param item 元素
   */
  add(type, item) {
    this.graph.add(type, item)
  }

  /**
   * graph 更新一个元素
   * @param item 元素 ID 或元素实例
   * @param model 需要更新的数据模型（详见antvg6 API）
   */
  update(item, model) {
    this.graph.update(item, model)
  }

  /**
   * graph 删除一个元素
   * @param item 元素 ID 或元素实例
   */
  remove(item) {
    const node = this.graph.findById(item.id)
    this.graph.remove(node)
  }
}
