<template>
  <div class="page">
    <div ref="mountNode" :id="pageId" class="graph-container" style="position: relative;"></div>
  </div>
</template>


<script>
import G6 from "@antv/g6/build/g6";
import { initBehavors } from "../../behavior";
export default {
  data() {
    return {
      resizeFunc: ()=>{},
      // g6绘制的div的id
      pageId: "graph-container",
      // g6 graph元素
      graph: null,
    };
  },
  props: {
    // 传入height
    height: {
      type: Number,
      default: 0
    },
    // 传入width
    width: {
      type: Number,
      default: 0
    },
    // 传入用于初始化数据的data
    data: {
      type: Object,
      default: () => {}
    }
  },
  created() {
    // 初始化G6 Behavors
    // 指向behavior文件夹中的index中export的function
    initBehavors();
  },
  mounted() {
    // Vue.nextTick用于延迟执行一段代码
    this.$nextTick(() => {
      this.init();
    });
  },
  methods: {
    init() {
      // 通过计算获得g6的height和width
      const height =  this.height - 145
      const width =  this.width - 575
      // 初始化Graph
      this.graph = new G6.Graph({
        container: "graph-container",
        height: height,
        width: width,
        modes: {
          // default mode下支持的 behavior
          default: [
            "drag-canvas",
            "zoom-canvas",
            "hover-node",
            "select-node",
            "hover-edge",
            "keyboard",
            "customer-events",
            "add-menu",
            "canvas-click"
          ],
          // 多选 mode 下支持的 behavior
          mulitSelect: ["mulit-select"],
          // 添加 edge 下支持的 behavior
          addEdge: ["add-edge", "drag-canvas", "zoom-canvas", "hover-edge", "keyboard"],
          // 移动节点时支持的 behavior
          moveNode:["drag-item"]
        }
      });
      // 通过获取 this.$parent 获取到父组件data中的editor、command
      const { editor, command } = this.$parent;
      // 触发editor的afterAddPage 事件 传入 graph 和 command
      editor.emit("afterAddPage", { graph: this.graph, command });
      this.initResizeEvent();
      // 调用 readData 方法
      this.readData();
    },
    /**
     * 利用传入的 data 对 g6 进行初始化
     */
    readData() {
      let data = this.data;
      this.graphData = this.data;
      let startID, endID, cycleID;
      if (data) {
        for(let i in data.nodes){
          //先遍历节点，找到开始、结束、循环节点
          switch (data.nodes[i].mType) {
            case 'startNode':
              startID = data.nodes[i].id;
              break;
            case 'endNode':
              endID = data.nodes[i].id;
              break;
            case 'cycleNode':
              cycleID = data.nodes[i].id;
              break;
          }
        }
        this.graph.read(data);
      }
    },
    /**
     * 注册resize的时候改变canvas大小的event
     */
    initResizeEvent() {
      const page = this.$refs['mountNode'];
      const graph = this.graph;
      this.resizeFunc = ()=>{
        graph.changeSize(page.offsetWidth, window.innerHeight - 90);
      };
      window.addEventListener("resize", this.resizeFunc);
    }
  }
};
</script>

<style scoped>
.page{
  margin-left:200px;
  margin-right: 200px;
}
</style>
