/**
 * hover-node 注册对节点上悬浮的事件
 */
export default {
    getEvents() {
        return {
            'node:mouseover': 'onMouseover',
            'node:mouseleave': 'onMouseleave',
            "node:mousedown": "onMousedown"
        };
    },
    /**
     * 处理鼠标移入节点上方时触发的事件
     * @param e event事件
     */
    onMouseover(e) {
        // 获取this对象
        const self = this;
        // 获取目标节点
        const item = e.item;
        // 获取当前的 graph 对象
        const graph = self.graph;
        // 获取节点的容器
        const group = item.getContainer();
        // 获取到当前操作对象的属性，如果存在 isOutPointOut 或 isOutPoint 属性
        if (e.target._attrs.isOutPointOut || e.target._attrs.isOutPoint) {
            // 遍历 group 中的点 即遍历 customNode 中的各个图形
            group.find(g => {
                // 如果是 anchor 输入点或者输出点 则将 fill 属性设为 #FFF
                if (g._attrs.isInPoint || g._attrs.isOutPoint) {
                    g.attr("fill", "#fff")
                }
                // 如果是 anchor 输出点
                if (g._attrs.isOutPoint) {
                    if (g._attrs.id === e.target._attrs.parent) {
                        group.find(gr => {
                            // 2020.1.11 改成了点模式和边模式 此处改变样式不需要
                            // if (gr._attrs.id === g._attrs.id) {
                            //     // 将选中的 anchor点 颜色重置且透明度设为1 即实心蓝色圆
                            //     gr.attr('fill', "#1890ff")
                            //     gr.attr('opacity',1)
                            // }
                        })
                    }
                    // 2020.1.11 改成了点模式和边模式 此处改变样式不需要
                    // 将选中的 anchor点 颜色重置且透明度设为1 即实心蓝色圆
                    // if (g._attrs.id === e.target._attrs.id) {
                    //     g.attr("fill", "#1890ff")
                    //     g.attr('opacity',1)
                    // }

                }
            });
            // 2020.1.11 改成了点模式和边模式 此处鼠标指针设置不需要
            // 将鼠标指针设为十字准星型
            // e.target.attr("cursor", "crosshair");

            // 修改了元素样式 调用paint方法让修改生效
            this.graph.paint();
        }
        // 如果当前节点状态为 selected
        if (item.hasState('selected')) {
            // 则不更新状态 以 selected 为当前状态
            return
        } else {
            // shouldUpadte？
            if (self.shouldUpdate.call(self, e)) {
                // 设置元素状态 hover 为 true
                graph.setItemState(item, 'hover', true);
            }
        }
        // 修改了元素状态 调用paint方法让修改生效
        graph.paint();
    },
    /**
     * 处理鼠标移出节点上方时触发的事件
     * @param e event事件
     */
    onMouseleave(e) {
        const self = this;
        const item = e.item;
        const graph = self.graph;
        const group = item.getContainer();
        // 重置anchor点的样式
        group.find(g => {
            // 将 anchor输入输出点 的 fill 属性置为 #FFF
            if (g._attrs.isInPoint || g._attrs.isOutPoint) {
                g.attr("fill", "#fff")
            }
        });
        if (self.shouldUpdate.call(self, e)) {
            // 如果当前 item 状态不为 selected 则置 hover 属性为 false
            if(!item.hasState('selected'))
            graph.setItemState(item, 'hover', false);
        }
        // 修改了元素样式与状态 调用paint方法让修改生效
        graph.paint();
    },
    /**
     * 处理鼠标点击节点时触发的事件
     * @param e event事件
     */
    onMousedown(e) {
        // 2020.1.11 此处操作逻辑更改为点模式与边模式
        // 当点击的元素为 anchor点 设 graph 的 mode 为 addEdge 添加一条边
        // if(e.target._attrs.isOutPoint ||e.target._attrs.isOutPointOut){
        //     this.graph.setMode('addEdge')
        // }else {
        //     // 否则认为是移动节点 设 mode 为 moveNode
        //     this.graph.setMode('moveNode')
        // }
        if(self.graph.getCurrentMode() !== "addEdge"){
            // 否则认为是移动节点 设 mode 为 moveNode
            this.graph.setMode('moveNode')
        }
    },

};
