export default (_self, h) => {
    return [
        h("el-select", {
                props: {
                    value: _self.obj.value || "",
                },
                attrs: {
                    placeholder: _self.obj.attr.placeholder || "请选择",
                    filterable: true
                },
                on: {
                    'change'(arr) {
                        _self.obj.value = arr;
                        _self.$emit('handleChangeVal', arr)
                    }
                }
            },
            _self.obj.attr.items.map(v => {
                return h("el-option", {
                    props: {
                        label: v.label_name,
                        value: v.label_value
                    }
                })
            }))
    ];
};

export let selectPersonConf = {
    // 对应数据库内类型
    type: 'select',
    // 是否可配置
    config: true,
    // 绑定的值
    value: "",
    // 验证错误提示信息
    ruleError: '该选项不能为空',
    attr: {
        label: '人员选择',
        require: true,
        placeholder: '',
        // 选项内数据
        items: [],
    },
    attrName: [
        {
            name: '控件名称',
            attribute: 'label',
            type: 'input',
            clientType: 'sell'
        },
        // 由于负责人是必填项 所以不需要显示出来让用户选择了
        {
            name: '是否必填',
            attribute: 'require',
            type: 'singleCheck',
            clientType: 'sell'
        },
        {
            name: 'Placeholder',
            attribute: 'placeholder',
            type: 'input',
            clientType: 'sell'
        },
    ]
};
