<template>
  <div>
    <ul class="el-scrollbar__view el-select-dropdown__list context-menu" ref="contextMenu">
      <li
        class="el-select-dropdown__item"
        v-for="menu in menus"
        :key="menu.key"
        @click="handleClick(menu)"
      >{{menu.name}}</li>
    </ul>
  </div>
</template>

<script>
import eventBus from "../../utils/eventBus";
export default {
  data() {
    return {
      // 菜单的项目
      menus: [{ key: 1, name: "菜单1" }, { key: 2, name: "菜单2" }]
    };
  },
  created() {
    // 绑定弹出菜单的右键事件
    this.bindEvent();
  },
  methods: {
    init() {},
    /**
     * 绑定弹出菜单的右键事件
     */
    bindEvent() {
      // 通过 eventBus 监听 contextmenuClick 事件
      eventBus.$on("contextmenuClick", e => {
        // 获取菜单对象
        const menu = this.$refs.contextMenu;
        // 将菜单的坐标设为当前鼠标指针坐标并改变display属性为可见
        // 此处需要减去侧边栏的200px（未知bug）
        menu.style.left = e.clientX - 200 + "px";
        // 此处需要减去顶栏的60px（未知bug）
        menu.style.top = e.clientY - 60 + "px";
        menu.style.display = "block";
      });
      // 通过 eventBus 监听 moustdown 事件
       eventBus.$on("mousedown", () => {
        const menu = this.$refs.contextMenu;
        menu.style.display = "none";
      });
    },
    /**
     * 处理点击事件
     * @param item 点击的item
     */
    handleClick(item) {
      alert(item.name);
      const menu = this.$refs.contextMenu;
      menu.style.display = "none";
    }
  }
};
</script>

<style>
.context-menu {
  position: absolute;
  border: 1px solid #e4e7ed;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
  margin: 5px 0;
  z-index: 1;
  display: none;
}

.context-menu li {
  cursor: pointer;
  font-size: 12px;
  height: 28px;
  line-height: 28px;
}
.context-menu li:hover {
  background-color: #f5f7fa;
}
</style>
