export default (_self, h) => {
  return [
    h("el-radio-group", {
      props: {
        value: _self.obj.value || "1"
      },
      on: {
        'input' (value) {
          // if (!_self.obj.name) {
          //   return false;
          // }
          _self.obj = Object.assign(_self.obj, {
            value
          });
          _self.$emit('handleChangeVal', value)
        }
      }
    }, _self.obj.attr.items.map(v => {
      return h("el-radio", {
        props: {
          label: v.label_value
        },
      }, v.label_name)
    }))
  ];
};

export let radioConf = {
  // 对应数据库内类型
  type: 'radio',
  // 是否可配置
  config: true,
  // 绑定的值
  value: [1, 2],
  attr: {
      label: '单选框',
      require: true,
      // 选项内数据
      items: [{ "label_value": 1, "label_name": "单选框1" }, { "label_value": 2, "label_name": "单选框2" }],
  },
  attrName: [
      {
          name: '标题',
          attribute: 'label',
          type: 'input',
          clientType: 'sell'
      },
      {
          name: '是否必填',
          attribute: 'require',
          type: 'singleCheck',
          clientType: 'sell'
      }
  ]
}
