<template>
    <G6Editor
            mode="edit"
            :data="flowData"
            :name="flowName"
            :id="flowID"
            :is-edit="isEdit"
            :status="status"
            v-if="show"
            style="margin: 10px">
    </G6Editor>
</template>

<script>
    import G6Editor from '../components/editor/index'
    import { editorAPI } from "@/globalAPI";
    export default {
        name: "Editor",
        data() {
            return {
                // 流程ID
                flowID: -1,
                // 流程名称
                flowName: "",
                // 是否为编辑状态
                isEdit: false,
                // 流程所属用户的ID
                userID: "",
                // 用于判断是否获取到数据 为真则渲染Editor
                show: false,
                // 流程数据
                flowData: {},
                // 传入数据类型
                status: ""
            }
        },
        components: {
            G6Editor
        },
        methods: {
            /**
             * 判断是否有传入 flowId 判断是否通过数据初始化 editor
             */
            getFlowData() {
                if (this.$route.params.flowId) {
                    this.flowID = this.$route.params.flowId;
                    this.flowName = this.$route.params.flowName;
                    this.userID = this.$route.params.userId;
                    this.isEdit = true;
                    this.status = this.$route.params.status;
                    if (this.status === "normal") {
                        // ajax请求获取表单数据
                        this.axios.post(editorAPI.downloadOneProcess, this.qs.stringify({
                            processId: this.flowID,
                            userId: this.userID
                        }))
                            .then((response) => {
                                if(response.data.resultCode === 1){
                                    this.flowData = JSON.parse(response.data.objData.content);
                                    this.flowName = response.data.objData.name;
                                    this.show = true;
                                }
                            })
                            .catch((error) => {
                                this.$message({
                                    type: "error",
                                    message: error.response.data.message
                                });
                            });
                    } else if(this.status === "draft") {
                        this.axios.get(editorAPI.draftContent, {
                            params: {
                                id: this.flowID
                            }
                        })
                                .then((response) => {
                                  if(response.data.resultCode === 1){
                                      this.flowData = JSON.parse(response.data.objData.content);
                                      this.flowName = response.data.objData.name;
                                      this.show = true;
                                  }
                                })
                                .catch((error) => {
                                    this.$message({
                                        type: "error",
                                        message: error.response.data.message
                                      });
                                });
                    }
                } else {
                    this.show = true;
                }
            }
        },
        created() {
            this.getFlowData();
        }
    }
</script>

