import eventBus from "../utils/eventBus";
export default {
    getEvents() {
        return {
            'node:contextmenu': 'onContextmenu',
            'mousedown': 'onMousedown',
            'canvas:click':'onCanvasClick'
        };
    },
    /**
     * 响应右键弹出菜单，使菜单出现
     * @param e event句柄
     */
    onContextmenu(e) {
        // 通过eventBus，触发contextmenuClick事件
        eventBus.$emit('contextmenuClick',e)
    },
    /**
     * 响应鼠标点击事件，使菜单消失
     * @param e event句柄
     */
    onMousedown(e) {
        // 通过eventBus，触发mousedown事件
        eventBus.$emit('mousedown',e)
    },
    /**
     * 响应 canvas 点击事件，使菜单消失
     * @param e event句柄
     */
    onCanvasClick(e){
        // 通过eventBus，触发canvasClick事件
        eventBus.$emit('canvasClick', e);
    }

};
