export default (_self, h) => {
    let icons = [];
    // 配置按钮
    icons.push(h('i', {
        attrs: {
            class: 'el-icon-setting',
        },
        on: {
            click() {
                _self.$emit('handleConfigElement', _self.index);
            }
        }
    }));
    // 删除按钮
    icons.push(h('i', {
        attrs: {
            class: 'el-icon-delete'
        },
        on: {
            click() {
                _self.$emit('handleRemoveElement', _self.index);
            }
        }
    }));
    return h('div', {
        class: {
            'item-icon': true
        }
    }, icons);
}
