export default (_self, h) => {
  return [
    h("el-checkbox-group", {
        props: {
          value: _self.obj.value || []
        },
        on: {
          'input'(arr) {
            _self.obj.value = arr;
            _self.$emit('handleChangeVal', arr)
          }
        }
      },
      _self.obj.attr.items.map(v => {
        return h("el-checkbox", {
          props: {
            label: v.label_value
          }
        }, v.label_name)
      }))
  ];
};

export let checkBoxConf = {
  // 对应数据库内类型
  type: 'checkbox',
  // 是否可配置
  config: true,
  // 绑定的值
  value: [1, 2],
  ruleError: '该选项不能为空',
  // 是否被渲染
  visibility: true,
  attr: {
      label: '多选框',
      require: true,
      // 选项内数据
      items: [{ "label_value": 1, "label_name": "多选框1" }, { "label_value": 2, "label_name": "多选框2" }],
  },
  attrName: [
      {
          name: '标题',
          attribute: 'label',
          type: 'input',
          clientType: 'sell'
      },
      {
          name: '是否必填',
          attribute: 'require',
          type: 'singleCheck',
          clientType: 'sell'
      }
  ]
}
