<template>
  <div style="margin: 20px 10px;">
      <link
          rel="stylesheet"
          type="text/css"
          href="//at.alicdn.com/t/font_1581731_z26piaggta.css"
      />
      <div style="margin-left: 35px;">
          <el-button @click="handleBack" style="margin-right: 15px;" icon="el-icon-back" type="info" plain v-if="isEdit">返回</el-button>
          <el-button @click="handleShowSaveDialog('saveAs')" icon="iconfont icon-save-as" type="primary" plain> 另存为</el-button>
          <el-button @click="handleShowSaveDialog('save')" style="margin-left: 15px;" icon="iconfont icon-save" type="success" plain> 保存</el-button>
      </div>
      <el-row class="formContainer">
          <el-col :span="7" class="formList">
              <!--左侧form-->
              <el-form ref="form" class="b-a" label-width="100px" style="overflow-y: scroll">
                  <h2 style="text-align: center">元素栏</h2>
                  <el-collapse style="padding: 15px;">
                      <el-collapse-item title="通用元素" name="1">
                          <!--list：用于实现拖拽的list，通常和内部v-for循环的数组为同一数组-->
                          <!--clone：-->
                          <!--options：配置项-->
                          <draggable :clone="cloneData" :list="commonList" :options="dragOptionsLeft">
                              <transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div">
                                  <renders
                                          v-for="(items, index) in commonList"
                                          :key="index"
                                          :ele="items.ele"
                                          :obj="items.obj">
                                  </renders>
                              </transition-group>
                          </draggable>
                      </el-collapse-item>
                      <!--<el-collapse-item title="表单基础元素" name="2">-->
                          <!--<draggable :clone="cloneData" :list="formBaseList" :options="dragOptionsLeft">-->
                              <!--<transition-group class="form-list-group" type="transition" :name="'flip-list'" tag="div">-->
                                  <!--<renders-->
                                          <!--v-for="(items, index) in formBaseList"-->
                                          <!--:key="index"-->
                                          <!--:ele="items.ele"-->
                                          <!--:obj="items.obj">-->
                                  <!--</renders>-->
                              <!--</transition-group>-->
                          <!--</draggable>-->
                      <!--</el-collapse-item>-->
                  </el-collapse>
              </el-form>
          </el-col>
          <el-col :span="17" class="formList">
              <!--右侧form-->
              <el-form ref="form" :model="formData" class="b-a" label-width="120px" style="overflow-y: scroll">
                  <h2 style="text-align: center">表单</h2>
                  <draggable
                          :clone="cloneData"
                          :list="dataList"
                          :options="dragOptionsRight">
                      <transition-group class="form-list-group-right" type="transition" :name="!drag ? 'flip-list' : null" tag="div">
                          <renders
                                  v-for="(items, index) in dataList"
                                  :key="index"
                                  :ele="items.ele"
                                  :obj="items.obj"
                                  :index="index"
                                  :config-icon="true"
                                  @handleChangeVal="val => handleChangeVal(val,items)"
                                  @handleConfigElement="handleConfig"
                                  @handleRemoveElement="handleRemoveElement">
                          </renders>
                      </transition-group>
                  </draggable>
              </el-form>
          </el-col>
      </el-row>
      <el-dialog
        title="编辑属性"
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        width="40%"
        :before-close="handleCancel">
        <el-form :model="attrForm" size="small" ref="attrForm">
            <!-- attr内属性 -->
            <el-form-item
                    v-for="(item) in attrsList"
                    :key="item.name"
                    :label="item.name"
                    :prop="item.attribute"
                    :rules="{
                        required: true, message: item.name + '不能为空', trigger: 'blur'
                    }"
                    label-width="100px">
                <el-input
                    v-model="attrForm[item.attribute]"
                    v-if="item.type === 'input'"
                    :disabled="item.clientType !== 'sell'">
                </el-input>
                <el-input-number
                    v-model="attrForm[item.attribute]"
                    v-if="item.type === 'inputNumber'"
                    :disabled="item.clientType !== 'sell'"
                    :precision="0">
                </el-input-number>
                <!--TODO: select待实现-->
                <!--<el-select v-model="value" placeholder="请选择" v-if="item.type === 'select'" :disabled="item.clientType !== 'sell'">-->
                    <!--<el-option-->
                        <!--v-for="item in options"-->
                        <!--:key="item.value"-->
                        <!--:label="item.label"-->
                        <!--:value="item.value">-->
                    <!--</el-option>-->
                <!--</el-select>-->
                <el-checkbox v-model="attrForm[item.attribute]" v-if="item.type === 'singleCheck'" :disabled="item.clientType !== 'sell'"></el-checkbox>
            </el-form-item>
            <!-- 单选框、多选框添加选项 -->
            <div v-if="attrForm.items">
                <el-divider>选项</el-divider>

                        <el-form-item
                                v-for="(item, index) in attrForm.items"
                                :label="'选项' + (index + 1)"
                                :key="item.value"
                                :prop="'items.' + index + '.label_name'"
                                :rules="{
                                required: true, message: '选项内容不能为空', trigger: 'blur'
                            }"
                                label-width="100px">
                            <el-row>
                                <el-col :span="21">
                            <el-input v-model="item.label_name" placeholder="选项内容"></el-input>
                                </el-col>
                            <el-col :span="3">
                                <el-button @click.prevent="removeOption(item)" size="small">删除</el-button>
                            </el-col>
                            </el-row>
                        </el-form-item>
                <el-form-item>
                    <el-button @click="addOption" style="margin-left: 100px">新增选项</el-button>
                </el-form-item>
            </div>
        </el-form>
        <span slot="footer">
            <el-button @click="handleCancel">取 消</el-button>
            <el-button type="primary" @click="handleOk">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog
              title="保存"
              :visible.sync="saveDialogVisible"
              width="30%">
          <el-form
                  ref="saveForm"
                  :model="saveForm"
                  label-width="80px"
                  :rules="saveRules">
              <el-form-item label="表单名称" prop="name">
                  <el-input v-model="saveForm.name" placeholder="请输入表单名称"></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer">
        <el-button @click="onSaveDialogClose">取 消</el-button>
        <el-button type="primary" @click="handleSubmit">保 存</el-button>
      </span>
      </el-dialog>
  </div>
</template>
<script>
// @ is an alias to /src
import draggable from "vuedraggable";
import { commonList, formBaseList } from "../components/custom/FormList";
import renders from "../components/custom/Render";
import { formAPI } from "../../../globalAPI";
import { randomn, genID,deepClone } from '../../../utils/index';
import { mapGetters } from "vuex";
export default {
  name: "home",
  data() {
    return {
      commonList,
      formBaseList,
      // 表单列表
      dataList: [],
      // 表单数据
      formData: {},
      // 表单名称
      formName: "",
      // 表单ID
      formID: "",
      // 是否为编辑状态
      isEdit: false,
      drag: false,
      // 编辑属性 dialog 显示控制
      dialogVisible: false,
      // 编辑属性 form 中的数据
      attrForm: {},
      // 编辑属性 form 中的属性
      attrsList: [],
      // 编辑属性 index
      editIndex: 0,
      // 保存 dialog 显示控制
      saveDialogVisible: false,
      // 保存 form 中的数据
      saveForm: {
          type: '',
          name: ''
      },
      // 保存 form 中的检验规则
      saveRules: {
          name: [
              { required: true, message: '请输入表单名称', trigger: 'blur' },
          ],
          // type: [
          //   { required: true, message: '请选择表单类型', trigger: 'change' }
          // ]
      }
    };
  },
  components: {
    draggable,
    renders
  },
  methods: {
    initData() {
        // 如果有传入 formId 则为编辑状态
        if (this.$route.params.formId) {
            this.formID = this.$route.params.formId;
            this.isEdit = true;
            this.axios
                .post(
                    formAPI.downloadOneForm,
                    this.qs.stringify({
                        formId: this.$route.params.formId,
                        userId: this.user.id
                    })
                )
                .then(response => {
                    if (response.data.resultCode === 1) {
                        this.dataList = JSON.parse(response.data.objData.content);
                        this.formName = response.data.objData.name;
                    }
                })
                .catch(error => {
                    this.$message({
                        type: "error",
                        message: error.response.data.message
                    });
                });
        }
    },
    handleBack() {
      // 返回上一页
      this.$router.go(-1);
    },
    handleShowSaveDialog(type) {
      switch (type) {
          // 另存为
          case "saveAs":
              // 显示 saveDialog
              this.saveDialogVisible = true;
              break;
          case "save":
              if(this.isEdit) {
                  // 编辑状态下 保存按钮作更新作用
                  this.handleUpdateForm();
              } else {
                  // 新建状态下 保存按钮和另存为按钮同一个作用
                  this.saveDialogVisible = true;
              }
              break;
      }
    },
    handleUpdateForm() {
        for(let i in this.dataList) {
            // 组件List value置空
            this.dataList[i].obj.value = "";
            this.dataList[i].obj.id = genID(2);
        }
        // 组建 param
        let param = new FormData();
        param.append("formName", this.formName);
        let blob = new Blob([JSON.stringify(this.dataList)], {type: "text/plain;charset=utf-8"});
        param.append("formFile", new File([blob], "form" + randomn(10) + ".txt"));
        param.append("oldFormId", this.formID);
        this.axios.post(formAPI.updateForm, param)
                .then((response) => {
                  if(response.data.resultCode === 1){
                    this.$message({
                        type: "success",
                        message: response.data.message
                    });

                  }
                })
                .catch((error) => {
                    this.$message({
                        type: "error",
                        message: error.response.data.message
                      });
                });
    },
    /**
     * 点击 saveDialog 中保存按钮时触发的 methods
     */
    handleSubmit() {
      this.$refs.saveForm.validate((valid) => {
          if (valid) {
              for(let i in this.dataList) {
                  // 组件List value置空
                  this.dataList[i].obj.value = "";
                  this.dataList[i].obj.id = genID(2);
              }
              console.log(this.dataList);
              console.log(JSON.stringify(this.dataList));
              // 组建 param
              let param = new FormData();
              param.append("formName", this.saveForm.name);
              let blob = new Blob([JSON.stringify(this.dataList)], {type: "text/plain;charset=utf-8"});
              param.append("formFile", new File([blob], "form" + randomn(10) + ".txt"));
              // ajax请求
              this.axios.post(formAPI.uploadForm, param)
                  .then((response) => {
                      // 判断是否成功
                      if(response.data.resultCode === 1){
                          this.$message({
                              message: response.data.message,
                              type: 'success'
                          });
                          this.resetSaveForm();
                          this.saveDialogVisible = false;
                      } else{
                          // 处理未知情况
                          this.$message({
                              message: response.data.message,
                          });
                      }
                  })
                  .catch((error) => {
                      this.$message({
                          type: 'error',
                          message: error.response.data.message
                      })
                  });
          }
      })
    },
    // https://github.com/SortableJS/Vue.Draggable#clone
    // 克隆,深拷贝对象
    cloneData(original) {
      // 深拷贝对象，防止默认空对象被更改
      return JSON.parse(JSON.stringify(original));
    },
    // 控件回填数据
    handleChangeVal(val, element) {
      this.$set(this.formData, element.obj.name, val);
    },
    /**
     * 处理点击配置icon时触发的事件
     * ！重点 需要
     */
    handleConfig(index) {
      this.editIndex = index;
      this.attrsList = this.dataList[index].obj.attrName;
      // 制作一个 attr 的副本 深拷贝一份给 attrForm
      const temp = Object.assign({}, this.dataList[index].obj);
      this.attrForm = deepClone(temp.attr);
      // Vue 不能检测到对象属性的添加或删除 因此需要这样使 Vue 检测到
      this.attrForm = Object.assign({}, this.attrForm);
      this.dialogVisible = true
    },
    /**
     * 处理点击删除icon时触发的事件
     */
    handleRemoveElement(index) {
      // 删除 dataList 中的数据
      this.dataList.splice(index, 1);
    },
    /**
     * 处理点击关闭 saveDialog 时的事件
     */
    onSaveDialogClose() {
        this.resetSaveForm();
        this.saveDialogVisible = false;
    },
    /**
     * 重置 saveForm 内容
     */
    resetSaveForm() {
        this.$refs.saveForm.resetFields();
        this.saveForm = {
            type: '',
            name: ''
        }
    },
    /**
     * 点击 saveDialog 确定时触发的事件
     */
    handleOk() {
        // 保存时将 attrForm 的数据传回给 dataList 中 obj.attr
        // 这里是关键！不这样的话会导致 Vue 感知不到数据发生变化 仍按照旧数据进行 render 会导致无法配置、删除
        // 此处代码等于调用 assign 使 Vue 响应式渲染 重新按照新数据渲染 即不会出错
        if(this.attrForm.items) {
            this.$refs["attrForm"].validate((valid) => {
                if(valid) {
                    // this.dataList[this.editIndex].obj.attr = Object.assign({}, this.dataList[this.editIndex].obj.attr, this.attrForm);
                    this.dataList[this.editIndex].obj.attr = deepClone(this.attrForm);
                    this.handleCancel();
                }
            })
        } else {
            // this.dataList[this.editIndex].obj.attr = Object.assign({}, this.dataList[this.editIndex].obj.attr, this.attrForm);
            this.dataList[this.editIndex].obj.attr = deepClone(this.attrForm);
            this.handleCancel();
        }
    },
    /**
     * 点击 saveDialog 取消时触发的事件
     */
    handleCancel() {
        // 关闭dialog
        this.dialogVisible = false;
        setTimeout(_ => {
            // 将 attrForm置空
            this.attrForm = {};
            // 重置核验
            this.$refs["attrForm"].resetFields();
        }, 500)
    },
    /**
     * 单选框、多选框配置 Dialog 中，点击删除选项触发的事件
     * @param item 要删除的选项
     */
    removeOption(item) {
        let index = this.attrForm.items.indexOf(item);
        if (index !== -1) {
            this.attrForm.items.splice(index, 1)
        }
    },
    addOption() {
        let length = this.attrForm.items.length;
        this.attrForm.items.push({
            label_name: "",
            label_value: length + 1
        })
    },
  },
  computed: {
    // 拖拽表单1配置项
    dragOptionsLeft() {
      return {
        // 动画时间 单位：ms
        animation: 50,
        // 当拖动列表单元时会生成一个副本作为影子单元来模拟被拖动单元排序的情况
        // 此配置项就是来给这个影子单元添加一个class
        // 我们可以通过这种方式来给影子元素进行编辑样式
        ghostClass: "ghost",
        // 分组
        // name相同的组可以互相拖动
        group: {
          name: "shared",
          pull: "clone",
          revertClone: false
        },
        // 禁止拖动排序
        sort: false
      };
    },
    // 拖拽表单2配置项
    dragOptionsRight() {
      return {
        animation: 50,
        ghostClass: "ghost",
        group: {
          // 只允许放置shared的控件,禁止pull
          put: ["shared"]
        },
      };
    },
      ...mapGetters({
          user: "user"
      })
  },
  created() {
    this.initData();
  }
};

</script>
<style>
.formContainer {
  padding: 20px;
}
.form-list-group-right {
  min-height: 60vh;
  padding: 20px !important;
}

.b-a {
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 80vh;
}
.formList {
  padding-left: 15px;
  padding-right: 15px;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.item-icon {
  transition: all 1.5s ease;
  position: absolute;
  top: -30px;
  right: 0px;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
}
.item-icon i {
  cursor: pointer !important;
  margin-right: 5px;
}

.items:hover .item-icon {
  transition: inherit;
  opacity: 1;
  max-height: 50px;
}
.form-list-group {
  padding: 20px !important;
}
</style>
