/**
 * 生成随机数
 * @param n n位
 * @returns {*}
 */
function randomn(n) {
    if (n > 21) return null
    var re =new RegExp("(\\d{" + n + "})(\\.|$)")
    var num = (Array(n-1).join(0) + Math.pow(10,n) * Math.random()).match(re)[1]
    return num
    // Array(n > num ? (n - ('' + num).length +1) : 0).join(0) + num 补位
}

/**
 * Parse the time to string
 * @param {(Object|string|number)} time
 * @param {string} cFormat
 * @returns {string}
 */
function renderTime(time, cFormat) {
    if (arguments.length === 0) {
        return null;
    }
    const format = cFormat || "{y}-{m}-{d} {h}:{i}:{s}";
    let date;
    if (typeof time === "object") {
        date = time;
    } else {
        if (typeof time === "string") {
            if (/^[0-9]+$/.test(time)) {
                // support "1548221490638"
                time = parseInt(time);
            } else {
                // support safari
                // https://stackoverflow.com/questions/4310953/invalid-date-in-safari
                time = time.replace(new RegExp(/-/gm), "/").replace(/T/, ' ').split(".")[0];
            }
        }

        if (typeof time === "number" && time.toString().length === 10) {
            time = time * 1000;
        }
        date = new Date(time);
    }
    const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay()
    };
    const time_str = format.replace(/{([ymdhisa])+}/g, (result, key) => {
        const value = formatObj[key];
        // Note: getDay() returns 0 on Sunday
        if (key === "a") {
            return ["日", "一", "二", "三", "四", "五", "六"][value];
        }
        return value.toString().padStart(2, "0");
    });
    return time_str;
}

//通过js的内置对象JSON来进行数组对象的深拷贝
function deepClone(obj) {
    let _obj = JSON.stringify(obj);
    let objClone = JSON.parse(_obj);
    return objClone;
}

function genID(length){
    return Number(Math.random().toString().substr(3,length) + Date.now()).toString(32);
}

export {
    randomn,
    renderTime,
    deepClone,
    genID
};
