import Vue from 'vue'
import VueRouter from 'vue-router'
import store from "../store";
import { Message } from "element-ui";
import FormBuilder from "../components/dragFormBuilder/views/Home"
import GraphEditor from "../views/Editor"

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Login',
    component: resolve => require(['../views/Login.vue'], resolve),
    meta: {
      title: '卖家端管理系统登录',
      requireLogin: false
    }
  },
  {
    path: '/main',
    component: resolve => require(['../views/Main.vue'], resolve),
    meta: {
      title: '首页',
      requireLogin: true
    },
    children:[
      {
          path: "/",
          name: "Home",
          component: () =>
              import("../components/home/index.vue"),
          meta: {
              title: "首页",
              requireLogin: true
          }
      },
      {
          path: 'formList',
          name: 'FormList',
          component: resolve => require(['../components/formList/index.vue'], resolve),
          meta: {
              title: '表单列表',
              requireLogin: true
          }
      },
      {
          path: 'flowList',
          name: 'FlowList',
          component: resolve => require(['../components/flowList/index.vue'], resolve),
          meta: {
              title: '流程列表',
              requireLogin: true
          }
      },
      {
          path: 'userList',
          name: 'UserList',
          component: resolve => require(['../components/userList/index.vue'], resolve),
          meta: {
              title: '用户列表',
              requireLogin: true
          }
      },
    ]
  },
  {
      path: '/graph',
      name: 'GraphEditor',
      component: GraphEditor,
      meta: {
          title: '流程编辑器',
          requireLogin: true
      },
  },
  {
      path: '/preview',
      name: 'Preview',
      component: resolve => require(['../components/editor/components/Previewer/Preview.vue'], resolve),
      meta: {
          title: '流程预览器',
          requireLogin: true
      }
  },
  {
      path: '/form',
      name: 'FormBuilder',
      component: FormBuilder,
      meta: {
          title: '表单编辑器',
          requireLogin: true
      }
  },
  {
    path: '/test',
    name: 'test',
    component: resolve => require(['../components/test/test.vue'], resolve),
    meta: {
      title: 'API测试',
      requireLogin: false
    }
  },
];

const router = new VueRouter({
  // base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  // to: Route: 即将要进入的目标 路由对象
  // from: Route: 当前导航正要离开的路由
  // next: Function: 一定要调用该方法来 resolve 这个钩子。执行效果依赖 next 方法的调用参数。
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  if (to.meta.requireLogin) {
    if (store.state.isLogin) {
      next();
    } else {
      next({
        path: "/",
        query: { redirect: to.fullPath }
      });
      Message({
        message: "请登录后操作",
        type: "warning"
      });
    }
  } else {
    next();
  }
});

