<!-- ！请务必看该文件的TODO！ -->
<template>
  <ul>
    <li
      v-for="(item,index) in list"
      :key="index"
      class="getItem"
      :data-shape="item.shape"
      :data-type="item.type"
      :data-size="item.size"
      draggable
      @dragstart="handleDragstart($event, item)"
      @dragend="handleDragEnd($event,item)">
      <span class="pannel-type-icon" :style="{background:'url('+item.image+')'}"></span>
      {{item.name}}
    </li>
  </ul>
</template>

<script>
import eventBus from "../../utils/eventBus";
import {nodeList} from "../../nodeList"
import { deepClone } from "@/utils";
export default {
  data() {
    return {
      // g6的绘制区域
      page: null,
      // command实体
      command: null,
      // 偏移的X/Y值
      offsetX: 0,
      offsetY: 0,
      // 节点列表
      list: nodeList
    };
  },
  created() {
    this.bindEvent();
  },
  methods: {
    /**
     * 处理拖动开始的事件
     * @param e event句柄
     */
    handleDragstart(e) {
      // 将偏移X/Y坐标赋值
      this.offsetX = e.offsetX;
      this.offsetY = e.offsetY;
    },
    /**
     * 处理拖动结束的事件，结束后新增节点
     * @param e event句柄
     */
    handleDragEnd(e, item) {
      let mItem = item;
      // 检测节点是否为开始、结束、循环节点，是则检测其唯一存在性
      if(item.mType === 'startNode' || item.mType === 'endNode' || item.mType === 'cycleNode') {
        let nodes = this.page.graph.save().nodes;
        for(let i in nodes){
            // 若检测到已经存在一个该类型节点，则 return 结束循环并报错
          if(nodes[i].mType === item.mType) {
            this.$notify.error({
              title: '错误',
              message: item.name + ' 在流程图中只存在一个'
            });
            return;
          }
        }
      }
      if (this.page) {
        // 获取 page 中的 graph 实例
        const graph = this.page.graph;
        // 是同步节点的情况下 一次拉两个节点出来
        if(item.mType === "syncNode") {
            let startPoint = {};
            let endPoint = {};
            let startID, endID;
            // 第一次建立节点 先建立同步头节点
            // 将 item 中的属性合并入 data 中 使用深拷贝方法
            startPoint = deepClone(mItem);
            // 为 data 中的偏移X/Y坐标赋值
            startPoint.offsetX = this.offsetX;
            startPoint.offsetY = this.offsetY;
            // 获取 X/Y 坐标
            const xy = graph.getPointByClient(e.x, e.y);
            startPoint.x = xy.x;
            startPoint.y = xy.y;
            // 定义 data 中的 size 属性
            startPoint.size = [];
            startPoint.size = mItem.size.split("*");
            // 定义 data 中的 type 为 node
            startPoint.type = "node";
            // 定义 data 中的 label
            startPoint.name = "同步头节点";
            startPoint.label = "同步头节点";
            // 将此刻的数据复制一份到 endPoint 用于建立尾结点  使用深拷贝方法
            endPoint = deepClone(startPoint);
            // 为同步头节点设定前后端商量的节点类型
            startPoint.nodeKind =  1;
            // 同步头节点可以绑定任务 设定属性
            // TODO: 如果改了 nodeList 中的属性，需要此处也做更改
            startPoint.attr = {
                bindForm: '',
                time: 0,
                inChargeGroupID: '',
                inChargeUserID: '',
                roomGroups: [],
                needDevice: false,
                needAuditing: false
            };
            // TODO: 如果改了 nodeList 中的属性，需要此处也做更改
            startPoint.attrName = [
                {
                    name: '绑定表单',
                    attribute: 'bindForm',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '节点负责人职位',
                    attribute: 'inChargeGroupID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '工作人',
                    attribute: 'inChargeUserID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '聊天室用户组',
                    attribute: 'roomGroups',
                    type: 'multiSelect',
                    clientType: 'sell'
                },
                {
                    name: '时间限制（单位：分钟）',
                    attribute: 'time',
                    type: 'inputNumber',
                    min: 0,
                    clientType: 'sell'
                },
                {
                    name: '是否需要机器',
                    attribute: 'needDevice',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                },
                {
                    name: '是否需要审核',
                    attribute: 'needAuditing',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                }
            ];
            // 以数据 data 调用 command 中的【新增节点】命令
            // 返回值为建立的点ID
            startID = this.command.executeCommand("add", [startPoint]);
            // 第二次建立节点 建立同步尾节点
            endPoint.offsetY += 100;
            endPoint.y += 100;
            // 定义 endPoint 中的 label
            endPoint.name = "同步尾节点";
            endPoint.label = "同步尾节点";
            // 定义 endPoint 中的同步开始头节点 ID
            endPoint.syncBeginID = startID;
            // 为同步尾节点设定前后端商量的节点类型
            endPoint.nodeKind =  2;
            // 以修改过后的数据 data 调用 command 中的【新增节点】命令
            // 返回值为建立的点ID
            endID = this.command.executeCommand("add", [endPoint]);
            // 通过ID找到对应的头节点
            const item = this.page.graph.findById(startID);
            const model = {
                syncEndID: endID
            };
            // 定义其 syncEndID 属性
            this.page.graph.updateItem(item, model);
        } else if(item.mType === "concurrencyNode") {
            let startPoint = {};
            let endPoint = {};
            let startID, endID;
            // 第一次建立节点 先建立同步头节点
            // 将 item 中的属性合并入 data 中  使用深拷贝方法
            startPoint = deepClone(mItem);
            // 为 data 中的偏移X/Y坐标赋值
            startPoint.offsetX = this.offsetX;
            startPoint.offsetY = this.offsetY;
            // 获取 X/Y 坐标
            const xy = graph.getPointByClient(e.x, e.y);
            startPoint.x = xy.x;
            startPoint.y = xy.y;
            // 定义 data 中的 size 属性
            startPoint.size = [];
            startPoint.size = mItem.size.split("*");
            // 定义 data 中的 type 为 node
            startPoint.type = "node";
            // 定义 data 中的 label
            startPoint.name = "并发头节点";
            startPoint.label = "并发头节点";
            // 将此刻的数据复制一份到 endPoint 用于建立尾结点  使用深拷贝方法
            endPoint = deepClone(startPoint);
            // 为并发头节点设定前后端商量的节点类型
            startPoint.nodeKind =  0;
            // 并发头节点可以绑定任务 设定属性
            // TODO: 如果改了 nodeList 中的属性，需要此处也做更改
            startPoint.attr = {
                bindForm: '',
                time: 0,
                inChargeGroupID: '',
                inChargeUserID: '',
                roomGroups: [],
                needDevice: false,
                needAuditing: false
            };
            // TODO: 如果改了 nodeList 中的属性，需要此处也做更改
            startPoint.attrName = [
                {
                    name: '绑定表单',
                    attribute: 'bindForm',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '节点负责人职位',
                    attribute: 'inChargeGroupID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '工作人',
                    attribute: 'inChargeUserID',
                    type: 'select',
                    clientType: 'sell',
                    options: []
                },
                {
                    name: '聊天室用户组',
                    attribute: 'roomGroups',
                    type: 'multiSelect',
                    clientType: 'sell'
                },
                {
                    name: '时间限制（单位：分钟）',
                    attribute: 'time',
                    type: 'inputNumber',
                    min: 0,
                    clientType: 'sell'
                },
                {
                    name: '是否需要机器',
                    attribute: 'needDevice',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                },
                {
                    name: '是否需要审核',
                    attribute: 'needAuditing',
                    type: 'singleCheck',
                    required: true,
                    clientType: 'sell'
                }
            ];
            // 以数据 data 调用 command 中的【新增节点】命令
            // 返回值为建立的点ID
            startID = this.command.executeCommand("add", [startPoint]);
            // 第二次建立节点 建立同步尾节点
            endPoint.offsetY += 100;
            endPoint.y += 100;
            // 定义 endPoint 中的 label
            endPoint.name = "并发尾节点";
            endPoint.label = "并发尾节点";
            // 定义 endPoint 中的同步开始头节点 ID
            endPoint.concurrencyBeginID = startID;
            // 为并发尾节点设定前后端商量的节点类型
            endPoint.nodeKind =  6;
            // 以修改过后的数据 data 调用 command 中的【新增节点】命令
            // 返回值为建立的点ID
            endID = this.command.executeCommand("add", [endPoint]);
            // 通过ID找到对应的头节点
            const item = this.page.graph.findById(startID);
            const model = {
                concurrencyEndID: endID
            };
            // 定义其 syncEndID 属性
            this.page.graph.updateItem(item, model);
        }else {
            // 将 item 中的属性合并入 data 中  使用深拷贝方法
            let data = deepClone(item);
            // 为 data 中的偏移X/Y坐标赋值
            data.offsetX = this.offsetX;
            data.offsetY = this.offsetY;
            // 获取 X/Y 坐标
            const xy = graph.getPointByClient(e.x, e.y);
            data.x = xy.x;
            data.y = xy.y;
            // 定义 data 中的 size 属性
            data.size = item.size.split("*");
            // 定义 data 中的 type 为 node
            data.type = "node";
            // 以数据 data 调用 command 中的【新增节点】命令
            this.command.executeCommand("add", [data]);
        }
      }
    },
    /**
     * 绑定event
     */
    bindEvent() {
      // 通过 eventBus 监听 afterAddPage 事件
      eventBus.$on("afterAddPage", page => {
        // 将本地属性page设为传入的值
        this.page = page;
        // 将本地属性command设为传入的值
        this.command = page.command;
      });
    }
  }
};
</script>

<style scoped>
.itempannel {
  height: 100%;
  position: absolute;
  left: 0px;
  z-index: 2;
  background: #f7f9fb;
  width: 200px;
  padding-top: 8px;
  border-right: 1px solid #e6e9ed;
}
.itempannel ul {
  padding: 0px;
  padding-left: 16px;
}
.itempannel li {
  color: rgba(0, 0, 0, 0.65);
  border-radius: 4px;
  width: 160px;
  height: 28px;
  line-height: 26px;
  padding-left: 8px;
  border: 1px solid rgba(0, 0, 0, 0);
  list-style-type: none;
}
.itempannel li:hover {
  background: white;
  border: 1px solid #ced4d9;
  cursor: move;
}

.itempannel .pannel-type-icon {
  width: 16px;
