import input from './commonControl/Input';
import radio from './commonControl/Radio';
import checkBox from './commonControl/CheckBox';
import photo from './commonControl/Photo'
import record from './commonControl/Record'
import video from './commonControl/Video'
import file from './commonControl/File'
import selectPerson from './flowControl/selectPerson'
import ItemIcon from './ItemIcon'
const form_item = {
  input,
  radio,
  checkBox,
  photo,
  record,
  video,
  file,
  selectPerson
};

export default {
  name: 'renders',
  render(h) {
    let itemIcon = this.configIcon && this.obj.config ? ItemIcon(this, h) : [];
    return h(
        // 标签名称
        'el-form-item',
        // 一个与模板中属性对应的数据对象。
        {
          // 组件的prop
          // https://cn.vuejs.org/v2/guide/render-function.html#%E6%B7%B1%E5%85%A5%E6%95%B0%E6%8D%AE%E5%AF%B9%E8%B1%A1
          props: {
            label: this.obj.attr.label + "：",
          },
          style: {
            'position': 'relative'
          },
          class: {
            items: true
          },
        },
        // 子级虚拟节点 (VNodes)，由 `createElement()` 构建而成
        form_item[this.ele](this, h).concat(itemIcon))
  },
  // 上面用到的props
  props: {
    // 当前控件的类型
    ele: {
      type: String,
      default: "input"
    },
    // 当前控件的配置
    obj: {
      type: Object,
      default: {}
    },
    // 是否显示配置Icon
    configIcon: {
      type: Boolean,
      default: false
    },
    // 当前控件的index,config 和 delete 会用到
    index: {
      type: Number,
      default: 0
    }
  }
}
