// 通用控件
import { inputConf } from "./commonControl/Input";
import { radioConf } from "./commonControl/Radio";
import { checkBoxConf } from "./commonControl/CheckBox";
import { photoConf } from "./commonControl/Photo";
import { videoConf } from "./commonControl/Video";
import { recordConf } from "./commonControl/Record";
import { fileConf } from "./commonControl/File"
// 表单基础元素
import { selectPersonConf } from "./flowControl/selectPerson"
// 通用控件
const commonControls = {
  input: inputConf,
  radio: radioConf,
  checkBox: checkBoxConf,
  photo: photoConf,
  video: videoConf,
  file: fileConf,
  record: recordConf
};
let commonList = [];
for (let i in commonControls) {
  commonList.push({
    ele: i,
    obj: commonControls[i]
  });
}

// 表单基础元素
const formBaseControls = {
  selectPerson: selectPersonConf
};
let formBaseList = [];
for (let i in formBaseControls) {
  formBaseList.push({
    ele: i,
    obj: formBaseControls[i]
  });
}

export {commonList, formBaseList};
